import React from 'react';
import {Container, Modal} from 'react-bootstrap';
import {Card, CardHeader, Divider, CardContent, Grid, Button} from '@material-ui/core';
import {gridSpacing} from '../../store/constant';
import MuiTypography from '@material-ui/core/Typography';
import SubCard from '../../ui-component/cards/SubCard';

import {ShowConsultations} from './ShowConsultations';

export const ShowSelectedPatient = (props) => {
    const [showConsultations, setShowConsultations] = React.useState(false);
    const [selectedPatient, setShowSelectedPatient] = React.useState([]);

    const showSelectedConsultations = (patient) => {
        setShowConsultations(true);
        setShowSelectedPatient(patient);
    };

    const closeConsultationsPopUp = () => {
        setShowConsultations(false);
    };

    const formatDate = (date) => {
        const options = {day: '2-digit', month: 'short', year: 'numeric', hour: '2-digit', minute: '2-digit'};
        const formattedDate = new Date(date).toLocaleDateString('en-US', options);

        // Split the formatted date into day, month, and year parts
        const [month, day, year, hour, minute] = formattedDate.split(' ');

        // Convert the month abbreviation to uppercase
        const capitalizedMonth = month.toUpperCase();

        // Return the formatted date with uppercase month abbreviation and desired format
        return `${day} ${capitalizedMonth} ${year} ${hour} ${minute}`;
    };
    const displayImg = (imgSrc) => {
        document.getElementById('full-image').src = imgSrc;
        var dImg = document.getElementById('image-viewer');
        dImg.style.display = 'block';
    };
    const hideImg = () => {
        var hImg = document.getElementById('image-viewer');
        hImg.style.display = 'none';
    };

    return (
        <>
            {showConsultations && <ShowConsultations patient={selectedPatient} handleCloseConsultations={closeConsultationsPopUp} />}
            <Container>
                <Grid container spacing={gridSpacing}>
                    <Grid item xs={12}>
                        <Card className="popup-container">
                            <CardHeader title="Consultation" />
                            <Divider />
                            <CardContent className="popup-body" id="popupCustom">
                                <Button id="closeBtn" onClick={() => props.handleClose()}>
                                    X
                                </Button>
                                <Grid container spacing={gridSpacing}>
                                    <Grid item xs={12} sm={6}>
                                        <SubCard title={props.patient.full_name}>
                                            <Grid container direction="column" id="poppins" spacing={1}>
                                                <Grid item>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Patient name: &nbsp;
                                                        <span id="small-text">{props.patient.full_name}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        ID/Iqama number: &nbsp;
                                                        <span id="small-text">{props.patient.iqama_number}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Copy of ID/Iqama number: &nbsp;
                                                        <span
                                                            id="small-text"
                                                            onClick={() => displayImg(props.patient.copy_of_iqama_number)}
                                                            style={{cursor: 'pointer', textDecoration: 'underline', color: 'blue'}}
                                                        >
                                                            Image
                                                        </span>
                                                        <div id="image-viewer" onClick={hideImg}>
                                                            <span className="closed">&times;</span>
                                                            <img className="modal-content" id="full-image" />
                                                        </div>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Date of birth: &nbsp;
                                                        <span id="small-text">{props.patient.date_of_birth}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Mobile number: &nbsp;
                                                        <span id="small-text">{props.patient.mobile_number}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        E-mail: &nbsp;
                                                        <span id="small-text">{props.patient.email}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Nationality: &nbsp;
                                                        <span id="small-text">{props.patient.nationality}</span>
                                                    </MuiTypography>
                                                    <MuiTypography variant="h3" gutterBottom>
                                                        Last login: &nbsp;
                                                        <span id="small-text">{formatDate(props.patient.last_login)}</span>
                                                    </MuiTypography>
                                                    <Button
                                                        variant="contained"
                                                        color="primary"
                                                        disableElevation
                                                        type="submit"
                                                        onClick={() => showSelectedConsultations(props.patient)}
                                                    >
                                                        Show Consultations
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                        </SubCard>
                                    </Grid>
                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </Container>
        </>
    );
};
